import axios from 'axios'

const AXIOS = axios.create({
  baseURL: `/api`,
  timeout: 1000
});


export default {
    getSystem(systemId) {
        return AXIOS.get(`/system` + systemId);
    },
    callHelloApi(){
      return AXIOS.get( `/hello` )
    },
    createSystem(name, description, status) {
        return AXIOS.post(`/system/` + name + '/' + description + '/' + status);
    }
}


